  /**
   * „Éï„Ç©„Éº„Ç´„ÇπÁÆ°ÁêÜÊ©üËÉΩ„ÇíË®≠ÂÆö
   */
  setupFocusManagement() {
    console.log('üéØ Setting up focus management...');

    try {
      // „Éá„Çπ„ÇØ„Éà„ÉÉ„Éó„Çµ„Ç§„Éâ„Éê„Éº„ÅÆ„Éï„Ç©„Éº„Ç´„ÇπÁÆ°ÁêÜ
      this.setupDesktopSidebarFocus();

      // „É¢„Éê„Ç§„É´„Çµ„Ç§„Éâ„Éê„Éº„ÅÆ„Éï„Ç©„Éº„Ç´„ÇπÁÆ°ÁêÜ
      this.setupMobileSidebarFocus();

      // „Ç≠„Éº„Éú„Éº„Éâ„Éä„Éì„Ç≤„Éº„Ç∑„Éß„É≥
      this.setupKeyboardNavigation();

      console.log('‚úÖ Focus management setup complete');
    } catch (error) {
      console.error('‚ùå Failed to setup focus management:', error);
    }
  }

  /**
   * „Éá„Çπ„ÇØ„Éà„ÉÉ„Éó„Çµ„Ç§„Éâ„Éê„Éº„ÅÆ„Éï„Ç©„Éº„Ç´„ÇπÁÆ°ÁêÜ
   */
  setupDesktopSidebarFocus() {
    const desktopSidebar = document.getElementById('desktop-sidebar');
    if (!desktopSidebar) {
      return;
    }

    // „Çµ„Ç§„Éâ„Éê„Éº„Å´„Éï„Ç©„Éº„Ç´„Çπ„ÅåÂΩì„Åü„Å£„ÅüÊôÇ„ÅÆÂá¶ÁêÜ
    desktopSidebar.addEventListener('focusin', () => {
      console.log('üéØ Desktop sidebar focused');
      this.showDesktopSidebar();
    });

    // „Çµ„Ç§„Éâ„Éê„Éº„Åã„Çâ„Éï„Ç©„Éº„Ç´„Çπ„ÅåÂ§ñ„Çå„ÅüÊôÇ„ÅÆÂá¶ÁêÜ
    desktopSidebar.addEventListener('focusout', (e) => {
      // „Éï„Ç©„Éº„Ç´„Çπ„Åå„Çµ„Ç§„Éâ„Éê„ÉºÂÜÖ„ÅÆ‰ªñ„ÅÆË¶ÅÁ¥†„Å´ÁßªÂãï„Åó„ÅüÂ†¥Âêà„ÅØË°®Á§∫„ÇíÁ∂≠ÊåÅ
      if (!desktopSidebar.contains(e.relatedTarget)) {
        console.log('üéØ Desktop sidebar focus lost');
        this.hideDesktopSidebar();
      }
    });

    // „Éä„Éì„Ç≤„Éº„Ç∑„Éß„É≥„Ç¢„Ç§„ÉÜ„É†„ÅÆ„Éï„Ç©„Éº„Ç´„ÇπÁÆ°ÁêÜ
    const navItems = desktopSidebar.querySelectorAll('.nav-item');
    navItems.forEach((item, index) => {
      item.addEventListener('focus', () => {
        this.showDesktopSidebar();
      });

      // „Ç≠„Éº„Éú„Éº„Éâ„Éä„Éì„Ç≤„Éº„Ç∑„Éß„É≥
      item.addEventListener('keydown', (e) => {
        this.handleNavigationKeydown(e, navItems, index);
      });
    });
  }

  /**
   * „É¢„Éê„Ç§„É´„Çµ„Ç§„Éâ„Éê„Éº„ÅÆ„Éï„Ç©„Éº„Ç´„ÇπÁÆ°ÁêÜ
   */
  setupMobileSidebarFocus() {
    const mobileSidebar = document.getElementById('mobile-sidebar');
    if (!mobileSidebar) {
      return;
    }

    // „É¢„Éê„Ç§„É´„Çµ„Ç§„Éâ„Éê„Éº„ÅÆ„Éï„Ç©„Éº„Ç´„ÇπÁÆ°ÁêÜ
    mobileSidebar.addEventListener('focusin', () => {
      console.log('üéØ Mobile sidebar focused');
      this.showMobileSidebar();
    });

    mobileSidebar.addEventListener('focusout', (e) => {
      if (!mobileSidebar.contains(e.relatedTarget)) {
        console.log('üéØ Mobile sidebar focus lost');
        this.hideMobileSidebar();
      }
    });
  }

  /**
   * „Ç≠„Éº„Éú„Éº„Éâ„Éä„Éì„Ç≤„Éº„Ç∑„Éß„É≥„ÇíË®≠ÂÆö
   */
  setupKeyboardNavigation() {
    document.addEventListener('keydown', (e) => {
      // Alt + M: „Çµ„Ç§„Éâ„Éê„Éº„ÇíÈñã„Åè/Èñâ„Åò„Çã
      if (e.altKey && e.key === 'm') {
        e.preventDefault();
        this.toggleSidebar();
      }

      // Escape: „Çµ„Ç§„Éâ„Éê„Éº„ÇíÈñâ„Åò„Çã
      if (e.key === 'Escape') {
        this.hideAllSidebars();
      }
    });
  }

  /**
   * „Éá„Çπ„ÇØ„Éà„ÉÉ„Éó„Çµ„Ç§„Éâ„Éê„Éº„ÇíË°®Á§∫
   */
  showDesktopSidebar() {
    const desktopSidebar = document.getElementById('desktop-sidebar');
    if (!desktopSidebar) {
      return;
    }

    // „É¢„Éê„Ç§„É´Ë°®Á§∫„ÅÆÂ†¥Âêà„ÅØ‰Ωï„ÇÇ„Åó„Å™„ÅÑ
    if (window.innerWidth < 768) {
      return;
    }

    desktopSidebar.classList.remove('hidden');
    desktopSidebar.classList.add('flex');
    this.sidebarVisible = true;

    // „Éï„Ç©„Éº„Ç´„ÇπÂèØËÉΩ„Å™ÊúÄÂàù„ÅÆË¶ÅÁ¥†„Å´„Éï„Ç©„Éº„Ç´„Çπ
    const firstFocusable = desktopSidebar.querySelector('.nav-item');
    if (firstFocusable) {
      firstFocusable.focus();
    }

    console.log('üéØ Desktop sidebar shown');
  }

  /**
   * „Éá„Çπ„ÇØ„Éà„ÉÉ„Éó„Çµ„Ç§„Éâ„Éê„Éº„ÇíÈùûË°®Á§∫
   */
  hideDesktopSidebar() {
    const desktopSidebar = document.getElementById('desktop-sidebar');
    if (!desktopSidebar) {
      return;
    }

    // „É¢„Éê„Ç§„É´Ë°®Á§∫„ÅÆÂ†¥Âêà„ÅØ‰Ωï„ÇÇ„Åó„Å™„ÅÑ
    if (window.innerWidth < 768) {
      return;
    }

    desktopSidebar.classList.add('hidden');
    desktopSidebar.classList.remove('flex');
    this.sidebarVisible = false;

    console.log('üéØ Desktop sidebar hidden');
  }

  /**
   * „É¢„Éê„Ç§„É´„Çµ„Ç§„Éâ„Éê„Éº„ÇíË°®Á§∫
   */
  showMobileSidebar() {
    const mobileSidebar = document.getElementById('mobile-sidebar');
    if (!mobileSidebar) {
      return;
    }

    // „Éá„Çπ„ÇØ„Éà„ÉÉ„ÉóË°®Á§∫„ÅÆÂ†¥Âêà„ÅØ‰Ωï„ÇÇ„Åó„Å™„ÅÑ
    if (window.innerWidth >= 768) {
      return;
    }

    mobileSidebar.classList.remove('hidden');
    mobileSidebar.classList.add('block');
    this.sidebarVisible = true;

    console.log('üéØ Mobile sidebar shown');
  }

  /**
   * „É¢„Éê„Ç§„É´„Çµ„Ç§„Éâ„Éê„Éº„ÇíÈùûË°®Á§∫
   */
  hideMobileSidebar() {
    const mobileSidebar = document.getElementById('mobile-sidebar');
    if (!mobileSidebar) {
      return;
    }

    // „Éá„Çπ„ÇØ„Éà„ÉÉ„ÉóË°®Á§∫„ÅÆÂ†¥Âêà„ÅØ‰Ωï„ÇÇ„Åó„Å™„ÅÑ
    if (window.innerWidth >= 768) {
      return;
    }

    mobileSidebar.classList.add('hidden');
    mobileSidebar.classList.remove('block');
    this.sidebarVisible = false;

    console.log('üéØ Mobile sidebar hidden');
  }

  /**
   * „Çµ„Ç§„Éâ„Éê„Éº„ÇíÂàá„ÇäÊõø„Åà
   */
  toggleSidebar() {
    if (window.innerWidth < 768) {
      // „É¢„Éê„Ç§„É´Ë°®Á§∫
      const mobileSidebar = document.getElementById('mobile-sidebar');
      if (mobileSidebar) {
        if (mobileSidebar.classList.contains('hidden')) {
          this.showMobileSidebar();
        } else {
          this.hideMobileSidebar();
        }
      }
    } else {
      // „Éá„Çπ„ÇØ„Éà„ÉÉ„ÉóË°®Á§∫
      const desktopSidebar = document.getElementById('desktop-sidebar');
      if (desktopSidebar) {
        if (desktopSidebar.classList.contains('hidden')) {
          this.showDesktopSidebar();
        } else {
          this.hideDesktopSidebar();
        }
      }
    }
  }

  /**
   * „Åô„Åπ„Å¶„ÅÆ„Çµ„Ç§„Éâ„Éê„Éº„ÇíÈùûË°®Á§∫
   */
  hideAllSidebars() {
    this.hideDesktopSidebar();
    this.hideMobileSidebar();
  }

  /**
   * „Éä„Éì„Ç≤„Éº„Ç∑„Éß„É≥„Ç≠„Éº„ÉÄ„Ç¶„É≥„ÇíÂá¶ÁêÜ
   */
  handleNavigationKeydown(e, navItems, currentIndex) {
    switch (e.key) {
      case 'ArrowDown': {
        e.preventDefault();
        const nextIndex = (currentIndex + 1) % navItems.length;
        navItems[nextIndex].focus();
        break;
      }
      case 'ArrowUp': {
        e.preventDefault();
        const prevIndex =
          currentIndex === 0 ? navItems.length - 1 : currentIndex - 1;
        navItems[prevIndex].focus();
        break;
      }
      case 'Home':
        e.preventDefault();
        navItems[0].focus();
        break;
      case 'End':
        e.preventDefault();
        navItems[navItems.length - 1].focus();
        break;
    }
  }

